
.loading-mask[data-v-2205d65a] {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0);
  display: table;
}
.loading-container[data-v-2205d65a] {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.loading-inner-mask[data-v-2205d65a],.loading-inner-mask-sc[data-v-2205d65a] {
  display: -webkit-box;
  -webkit-box-orient: horizontal;
  -webkit-box-pack: center;
  -webkit-box-align:center;
  width: 80px;
  height: 80px;
  background-color: rgba(0, 0, 0, .7);
  margin: 0 auto;
  text-align: center;
  border-radius: 3px;
  position: relative;
}
.loading-inner-mask-sc[data-v-2205d65a]{
  width:120px!important;
  background-color: transparent;
}
.loading-inner-mask.loadinginfo[data-v-2205d65a]::after {
  content: '加载中...';
  position: absolute;
  width: 80px;
  left: 0;
  bottom: 10px;
  color: #fff;
  font-size: 12px;
  letter-spacing: 2px;
}

/*type one*/
#rotate-loading[data-v-2205d65a] {
   -webkit-animation: rt 1.5s ease infinite;
           animation: rt 1.5s ease infinite;
}

/*type two*/
#scale-circular-1[data-v-2205d65a],
#scale-circular-2[data-v-2205d65a],
#scale-circular-3[data-v-2205d65a]{
  display:inline-block;
  width:12px;
  height:12px;
  background:orange;
  border-radius:100%;
  vertical-align: middle;
  margin:0 auto;
  -webkit-box-flex: 1;
}
