  /*animation: type2-loading-ani2 .5s linear infinite alternate;*/
/*}*/

/*#scale-circular-2{*/
  /*animation: type2-loading-ani2-1 .5s linear infinite alternate;*/
/*}*/

/*#scale-circular-3{*/
  /*animation: type2-loading-ani2-1 .5s linear infinite alternate;*/
/*}*/
.sc[data-v-2205d65a]{
  -webkit-animation: type2-loading-ani2 .4s linear infinite alternate;
          animation: type2-loading-ani2 .4s linear infinite alternate;
}
.loadinginfo #rotate-loading[data-v-2205d65a]{
  margin-top: -10px;
}
@-webkit-keyframes rt {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rt {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes type2-loading-ani2 {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(.1);
            transform: scale(.1);
}
}
@keyframes type2-loading-ani2 {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(.1);
            transform: scale(.1);
}
}
.default[data-v-2205d65a]{
  width: 60px;
  height: 60px;
}
.loadinginfo[data-v-2205d65a]{
  width: 80px;
  height: 80px;
}



